.chat-box {
    position: fixed;
    display: flex;
    flex-direction: column;
    right: 32px;
    bottom: 118px;
    width: 386px;
    height: calc(100% - 236px);
    max-height: 550px;
    min-height: 142px;
    background-color: #223870;
    border-radius: 0.3125rem;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    transition: 0.7s;
    filter: drop-shadow(4px 4px 4px rgba(0,0,0,0.3));
    * {
        width: 50%;
        height: 50%;
        color: white;
    }
}

#fade-in {
    height: 64px;
    opacity: 0;
    transition: all .75s ease;
}

#fade-in.show {
    opacity: 1;
    height: calc(100% - 236px);
}

.chat-header {
    width: 100%;
    height: 64px;
    max-height: 64px;
    border-radius: 0.3125rem;
    padding-left: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.chat-title {
    display: flex;
    align-items: center;
    justify-items: center;
    padding: 10px;
    font-size: medium;
    white-space: nowrap;
    vertical-align: center;
    text-align: center;
}

.chat-area {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: none;
    background-color: white;
    box-shadow: inset 0px -4px 16px rgba(0,0,0,0.1);
    
}
.chat-area::-webkit-scrollbar {
    width: 8px;
}
.chat-area::-webkit-scrollbar-track {
    // box-shadow: inset 0 0 8px rgba(100, 100, 100, 0.5);
    border-radius: 4px;
    background: white;
    margin: 17px;
}
.chat-area::-webkit-scrollbar-thumb {
    background: #acacac;
    border-radius: 4px;
}
.chat-area::-webkit-scrollbar-thumb:hover {
    background: #777;
}


.chat-footer {
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 64px;
    border-radius: 0 0 0.3125rem 0.3125rem;

    .send-text-area {
        width: 100%;
        height: 100%;
        font-size: 14px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        color: black;
        resize: none;
        outline: none;
        border-radius: 0.5rem;
        margin:  0.5rem;
        padding: 0.7rem;
        height: fit-content;
        overflow: auto;   
        border: solid 1px rgb(182, 182, 182);

    }

    .send-text-area::-webkit-scrollbar {
        display: none;
      }

    .send-button {
        display: flex;
        align-items: center;
        justify-content: center;
        height:36px;
        width: 45px;
        margin: 0.5rem 0.5rem 0.5rem 0;
        border-radius: 50%;
        transition: 0.7s;
        cursor: pointer;

        * {
            color: rgb(82, 81, 81);
        }
    }
    .send-button:hover {
        background-color: rgb(213, 213, 213);
        transition: 0.3s;
        transform: scale(115%);
    }
    
    .send-button:active {
        background-color: rgb(146, 146, 146);
        transform: scale(100%);
        transition: 0s;
    }
}

.message-list {
    width: auto;
    height:fit-content;
    display: flex;
    flex-direction: column;
    list-style-type:none;
    padding: 0.5rem;
    margin: 0;
    overflow: hidden;
}

@media only screen and (max-width: 600px) {
    
    .chat-box {
        width: calc(100% - 32px);
        right: 16px;
        bottom: 102px;
    }
}